:root {
    font-size: 60%;

    --color-background: rgb(0, 0, 0);
    --color-menu-lateral-bg: rgb(42, 42, 42);
    --color-menu-lateral-text: #ececec;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body #root {
    height: 100vh;
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
}

.App {
    width: 100vw;
}
body {
    background-color: var(--color-background);
}

body,
input,
button,
textarea {
    font: 500 1.6rem Poppins;
}

.container {
    width: 90vw;
}

@font-face {
    font-family: 'SignPainter';
    src: url('../fonts/SignPainter-HouseScript.ttf') format('truetype');
}

@media (min-width: 700px) {
    :root {
        font-size: 62.5%;
    }
}